<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <nav-bar></nav-bar>
    <h1>my-blogs</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="(item, index) in filteredBlogs" :key="index" class="article">
      <router-link :to="'blog/' + item.id">
        <h2 v-rainbow>{{item.title | to-uppercase}}</h2>  
      </router-link>
      <article>{{item.content}}</article>
    </div>
    <div class="pngImg">
      <img src='../assets/猫咪.png'>
    </div>
  </div>
</template>

<script>
import NavBar from './navbar/NavBar'

import { request } from "../network/request";

export default {
  name: "ShowBlogs",
  components: {
    NavBar
  },
  data() {
    return {
      blogs: [],
      search: ""
    }
  },
  created() {
    request({
      url: '/posts.json',
      method: 'get'
    }).then(res => {
      // this.blogs = data.data.slice(0, 10);
      var blogsArray = []
      for(let key in res.data) {
        // console.log(key);
        // console.log(res.data[key]);
        res.data[key].id = key
        blogsArray.push(res.data[key])
      }
      console.log(blogsArray);
      this.blogs = blogsArray
    })
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search)
      })
    }
  },
  filters: {
    // "to-uppercase": value => value.toUpperCase()
    toUppercase(value) {
      return value.toUpperCase()
    }
  },
  directives: {
    'rainbow': {
      bind(el, binding, vnode) {
        el.style.color = "#" + Math.random().toString(16).slice(2, 8);
      }
    }
  }
}
</script>

<style>
  #show-blogs {
    margin: 72px auto;
    max-width: 800px;
  }
  #show-blogs a {
    color: #444;
    text-decoration: none;
  }
  .article {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background-color: rgb(224, 255, 195);
    border-radius: 10px;
  }

  input[type="text"] {
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 5px;
  }

  .pngImg {
    position: fixed;
    bottom: 20px;
    right: 0;
  }
  .pngImg img {
    width: 130px;
    height: 186px;
  }
</style>


