<template>
  <div>
    <nav>
      <ul>
        <li>
          <router-link to="/" exact>blog</router-link>
          <router-link to="/add" exact>add</router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "BlogHeader"
}
</script>

<style scoped>
  ul {
    list-style-type: none;
    text-align: center;
    margin: 0;
    padding-left: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 4px;
    border-radius: 15px;
  }
  nav {
    background-color: rgb(255, 226, 253);
    padding: 15px 0;
    border-radius: 30px;
  }
  .router-link-active {
    background-color: rgb(255, 108, 248);
    color: #444;
  }
</style>


